"use client"

import { useState, useRef, useEffect } from "react"
import { Play, Pause, ChevronLeft, ChevronRight } from "lucide-react"

interface VideoItem {
  id: number
  src: string
  poster: string
  title: string
}

const videos: VideoItem[] = [
  {
    id: 1,
    src: "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Video%202025-12-03%20at%2015.08.48_8c61c856-C5dXLq3npZ4iCmbbVCurE7QoxY6tcx.mp4",
    poster: "/fashion-model-hoodie-street-style.jpg",
    title: "Street Style Collection",
  },
  {
    id: 2,
    src: "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Video%202025-12-03%20at%2015.08.48_8c61c856-C5dXLq3npZ4iCmbbVCurE7QoxY6tcx.mp4",
    poster: "/fashion-model-casual-wear-blue-tones.jpg",
    title: "Casual Elegance",
  },
  {
    id: 3,
    src: "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Video%202025-12-03%20at%2015.08.48_8c61c856-C5dXLq3npZ4iCmbbVCurE7QoxY6tcx.mp4",
    poster: "/fashion-model-urban-streetwear.jpg",
    title: "Urban Vibes",
  },
  {
    id: 4,
    src: "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Video%202025-12-03%20at%2015.08.48_8c61c856-C5dXLq3npZ4iCmbbVCurE7QoxY6tcx.mp4",
    poster: "/fashion-model-premium-clothing.jpg",
    title: "Premium Looks",
  },
  {
    id: 5,
    src: "https://hebbkx1anhila5yf.public.blob.vercel-storage.com/WhatsApp%20Video%202025-12-03%20at%2015.08.48_8c61c856-C5dXLq3npZ4iCmbbVCurE7QoxY6tcx.mp4",
    poster: "/fashion-model-minimalist-style.jpg",
    title: "Minimalist Style",
  },
]

export function VideoCarousel() {
  const [playingId, setPlayingId] = useState<number | null>(null)
  const [scrollPosition, setScrollPosition] = useState(0)
  const containerRef = useRef<HTMLDivElement>(null)
  const videoRefs = useRef<Map<number, HTMLVideoElement>>(new Map())

  const handlePlay = (id: number) => {
    // Pause any currently playing video
    if (playingId !== null && playingId !== id) {
      const prevVideo = videoRefs.current.get(playingId)
      if (prevVideo) {
        prevVideo.pause()
        prevVideo.currentTime = 0
      }
    }

    const video = videoRefs.current.get(id)
    if (video) {
      if (playingId === id) {
        video.pause()
        setPlayingId(null)
      } else {
        video.play()
        setPlayingId(id)
      }
    }
  }

  const scroll = (direction: "left" | "right") => {
    if (containerRef.current) {
      const scrollAmount = direction === "left" ? -320 : 320
      containerRef.current.scrollBy({ left: scrollAmount, behavior: "smooth" })
    }
  }

  const handleScroll = () => {
    if (containerRef.current) {
      setScrollPosition(containerRef.current.scrollLeft)
    }
  }

  useEffect(() => {
    const container = containerRef.current
    if (container) {
      container.addEventListener("scroll", handleScroll)
      return () => container.removeEventListener("scroll", handleScroll)
    }
  }, [])

  const canScrollLeft = scrollPosition > 0
  const canScrollRight = containerRef.current
    ? scrollPosition < containerRef.current.scrollWidth - containerRef.current.clientWidth - 10
    : true

  return (
    <section className="py-12 sm:py-16 lg:py-20 bg-[#E7F0FA]">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="text-center mb-8 sm:mb-12">
          <p
            className="text-xs sm:text-sm uppercase tracking-[0.2em] sm:tracking-[0.3em] text-[#2E5E99] mb-3 sm:mb-4"
            style={{ fontFamily: "var(--font-body)" }}
          >
            See What's Trending
          </p>
          <h2 className="text-2xl sm:text-3xl lg:text-5xl font-bold text-[#0D2440]">What Our Fellows Say</h2>
        </div>

        <div className="relative">
          {/* Left Arrow */}
          <button
            onClick={() => scroll("left")}
            className={`absolute left-0 top-1/2 -translate-y-1/2 z-10 w-10 h-10 sm:w-12 sm:h-12 rounded-full bg-white shadow-lg flex items-center justify-center transition-all hover:bg-[#2E5E99] hover:text-white -translate-x-1/2 ${
              canScrollLeft ? "opacity-100" : "opacity-0 pointer-events-none"
            }`}
          >
            <ChevronLeft className="h-5 w-5 sm:h-6 sm:w-6" />
          </button>

          {/* Video Container */}
          <div
            ref={containerRef}
            className="flex gap-4 sm:gap-6 overflow-x-auto scrollbar-hide scroll-smooth pb-4"
            style={{ scrollbarWidth: "none", msOverflowStyle: "none" }}
          >
            {videos.map((video) => (
              <div
                key={video.id}
                className="relative flex-shrink-0 w-[260px] sm:w-[300px] lg:w-[340px] aspect-[9/16] rounded-xl sm:rounded-2xl overflow-hidden bg-[#0D2440] group cursor-pointer shadow-lg hover:shadow-xl transition-shadow"
                onClick={() => handlePlay(video.id)}
              >
                <video
                  ref={(el) => {
                    if (el) videoRefs.current.set(video.id, el)
                  }}
                  className="w-full h-full object-cover"
                  loop
                  muted
                  playsInline
                  poster={video.poster}
                  onEnded={() => setPlayingId(null)}
                >
                  <source src={video.src} type="video/mp4" />
                </video>

                {/* Gradient overlay */}
                <div
                  className={`absolute inset-0 bg-gradient-to-t from-[#0D2440]/80 via-transparent to-transparent transition-opacity ${playingId === video.id ? "opacity-0" : "opacity-100"}`}
                />

                {/* Play button */}
                <button
                  className={`absolute bottom-4 left-4 w-12 h-12 sm:w-14 sm:h-14 rounded-full bg-white/90 backdrop-blur-sm flex items-center justify-center shadow-lg transition-all hover:scale-110 hover:bg-white ${
                    playingId === video.id ? "opacity-0" : "opacity-100"
                  }`}
                  onClick={(e) => {
                    e.stopPropagation()
                    handlePlay(video.id)
                  }}
                >
                  {playingId === video.id ? (
                    <Pause className="h-5 w-5 sm:h-6 sm:w-6 text-[#0D2440]" />
                  ) : (
                    <Play className="h-5 w-5 sm:h-6 sm:w-6 text-[#0D2440] ml-1" />
                  )}
                </button>

                {/* Pause button when playing */}
                {playingId === video.id && (
                  <button
                    className="absolute bottom-4 left-4 w-12 h-12 sm:w-14 sm:h-14 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
                    onClick={(e) => {
                      e.stopPropagation()
                      handlePlay(video.id)
                    }}
                  >
                    <Pause className="h-5 w-5 sm:h-6 sm:w-6 text-white" />
                  </button>
                )}

                {/* Title overlay */}
                <div
                  className={`absolute bottom-4 left-20 sm:left-24 right-4 transition-opacity ${playingId === video.id ? "opacity-0" : "opacity-100"}`}
                >
                  <p
                    className="text-white text-sm sm:text-base font-medium truncate"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    {video.title}
                  </p>
                </div>
              </div>
            ))}
          </div>

          {/* Right Arrow */}
          <button
            onClick={() => scroll("right")}
            className={`absolute right-0 top-1/2 -translate-y-1/2 z-10 w-10 h-10 sm:w-12 sm:h-12 rounded-full bg-white shadow-lg flex items-center justify-center transition-all hover:bg-[#2E5E99] hover:text-white translate-x-1/2 ${
              canScrollRight ? "opacity-100" : "opacity-0 pointer-events-none"
            }`}
          >
            <ChevronRight className="h-5 w-5 sm:h-6 sm:w-6" />
          </button>
        </div>

        {/* Scroll indicators for mobile */}
        <div className="flex justify-center gap-2 mt-6 sm:hidden">
          {videos.map((_, index) => (
            <div
              key={index}
              className={`w-2 h-2 rounded-full transition-colors ${
                Math.floor(scrollPosition / 280) === index ? "bg-[#2E5E99]" : "bg-[#7BA4D0]/40"
              }`}
            />
          ))}
        </div>
      </div>
    </section>
  )
}
